@charset "UTF-8";

// Default Variables

// Slick icon entity codes outputs the following
// "\2190" outputs ascii character "←"
// "\2192" outputs ascii character "→"
// "\2022" outputs ascii character "•"

$slick-font-path: '../fonts/' !default;
$slick-font-family: '../fonts/slick' !default;
$slick-loader-path: './' !default;
$slick-arrow-color: white !default;
$slick-dot-color: black !default;
$slick-dot-color-active: $slick-dot-color !default;
$slick-prev-character: '\2190' !default;
$slick-next-character: '\2192' !default;
$slick-dot-character: '\2022' !default;
$slick-dot-size: 6px !default;
$slick-opacity-default: 0.75 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: 0.25 !default;

@function slick-image-url($url) {
  @if function-exists(image-url) {
    @return image-url($url);
  } @else {
    @return url($slick-loader-path + $url);
  }
}

@function slick-font-url($url) {
  @if function-exists(font-url) {
    @return font-url($url);
  } @else {
    @return url($slick-font-path + $url);
  }
}

/* Slider */

.slick-list {
  .slick-loading & {
    background: #fff slick-image-url('../images/slider/ajax-loader.gif') center
      center no-repeat;
  }
}

/* Icons */
@if $slick-font-family == 'slick' {
  @font-face {
    font-family: 'slick';
    src: slick-font-url('slick.eot');
    src: slick-font-url('slick.eot?#iefix') format('embedded-opentype'),
      slick-font-url('slick.woff') format('woff'),
      slick-font-url('slick.ttf') format('truetype'),
      slick-font-url('slick.svg#slick') format('svg');
    font-weight: normal;
    font-style: normal;
  }
}

/* Arrows */

.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
  &:hover,
  &:focus {
    outline: none;
    background: transparent;
    color: transparent;
    &:before {
      opacity: $slick-opacity-on-hover;
    }
  }
  &.slick-disabled:before {
    opacity: $slick-opacity-not-active;
  }
  &:before {
    content: '';
    font-family: $slick-font-family;
    font-size: 20px;
    line-height: 1;
    color: $slick-arrow-color;
    opacity: $slick-opacity-default;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

.slick-prev.slick-arrow {
  background-image: url(../images/slider/arrow-back.svg);
  background-repeat: no-repeat;
  outline: none;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 99%;
  left: 27%;

  @media (min-width: 768px) {
    top: 98.5%;
    left: 39%;
  }

  @media (min-width: 1200px) {
    top: 98.5%;
    left: 41%;
  }

  &:hover,
  &:focus {
    background-image: url(../images/slider/arrow-back-hover.svg);
  }
}

.slick-next.slick-arrow {
  background-image: url(../images/slider/arrow-next.svg);
  background-repeat: no-repeat;
  outline: none;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 99%;
  right: 23%;

  @media (min-width: 768px) {
    top: 98.5%;
    right: 39%;
  }

  @media (min-width: 1200px) {
    top: 98.5%;
    right: 41%;
  }

  &:hover,
  &:focus {
    background-image: url(../images/slider/arrow-next-hover.svg);
    stroke: #ffa5ba;
  }
}

/* Dots */

.slick-dotted.slick-slider {
  margin-bottom: 30px;
}
.dots-slik {
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
  li {
    position: relative;
    display: inline-block;
    height: 20px;
    width: 20px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;
    button {
      border: 0;
      background: transparent;
      display: block;
      height: 20px;
      width: 20px;
      outline: none;
      line-height: 0px;
      font-size: 0px;
      color: transparent;
      padding: 5px;
      cursor: pointer;
      &:hover,
      &:focus {
        outline: none;
        &:before {
          opacity: $slick-opacity-on-hover;
        }
      }
      &:before {
        position: absolute;
        top: 0;
        left: 0;
        content: $slick-dot-character;
        width: 20px;
        height: 20px;
        font-family: $slick-font-family;
        font-size: 40px;
        line-height: 20px;
        text-align: center;
        color: #d9d9d9;
        opacity: $slick-opacity-not-active;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
      }
    }
    &.slick-active button:before {
      background-image: url(../images/slider/dotcurrent.svg);
      content: '';
      width: 12px;
      background-repeat: no-repeat;
      opacity: 1;
      background-position: center;

      // color: $slick-dot-color-active;
      // opacity: $slick-opacity-default;
    }
  }
}

